body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.s-card {
  margin: 20px;
}

.s-card-image {
  object-fit: cover;
  filter: grayscale(100%);
  border-radius: 10px;
  width: 300px;
  height: 480px;
  box-shadow: 0px 0px 3px 1px #ccc;
}

.s-card-info {
  margin-top: 10px;
  min-height: 100px;
}

.s-card-title {
  font-size: 24px;
  margin: 0px;
}

.s-card-sub-title {
  font-size: 16px;
  margin: 0px;
}

.parallax-inline-text {
  background: #fff;
  left: 50%;
  top: 50%;
  position: absolute;
  padding: 20px;
  transform: translate(-50%, -50%);
  max-width: 850px;
  text-align: center;
}